body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

  .sidenav {
    height: 100%;
    width: 80px;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #a74d4d;
    overflow-x: hidden;
    text-align:center;
  }
  .nav-control{
    position:absolute;
    bottom:50%;
    left:0;
    right:0;
    margin-left:auto;
    margin-right:auto;
    list-style-type: none;
  }
  .nav-control li{
    margin:20px 0px 20px 0px;
  }
  .nav-network{
    position:absolute;
    bottom:0;
    left:0;
    right:0;
    margin-left:auto;
    margin-right:auto;
  }
@media only screen and (min-width: 992px) { 
    .bg-home{
        background-color:#252424;
        min-height:100vh;
    }
    .bg-home-content h2{
        color:white;
        font-family: Oswald;
        font-size: 48px;
        letter-spacing: 5px;
    }
    .home-content h2{
        margin: 0px 0px 0px 250px;
        line-height:50px;
    }
    .home-content h2:first-child{
        margin-top:325px;
    }
    hr{
        margin-top:20px;
        margin-bottom:20px;
        width:75%;
        margin-left:220px;
        color:white;
        height: 1px;
        border:none;
        background-color:white;
    }
    .home-content p{
        font-size:18px;
        color:#5d5c5c;
        margin: 0px 0px 0px 250px;
        font-family: oswald;
        letter-spacing: 2px;
    }
    .home-content p:last-child{
        margin-top:20px;
        color:white;
        border:2px solid #008f91;
        width:120px;
        text-align: center;
        padding:5px;
        border-radius:10px;
        font-size:14px;
    }
    .home-content p:last-child:hover{
        background-color:#008f91;
        cursor:pointer;
    }
    .bg-home-picture p{
        min-height:400px;
        border:2px solid white;
        border-radius: 300px;
        width:400px;
        margin-top:275px;
        margin-left:300px;
        background-color:white;
    }
} 
@media only screen and (max-width: 600px) {
    .sidenav {
        background-color:red;
        text-align:center;
        width:100%;
    }
    .sidenav p{
        margin:0;
        display:inline-block;
    }
    .nav-logo{
        display:none;
    }
}

